*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: medieval;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

* {
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  overflow-x: hidden; /* Prevent side-scrolling */
  line-height: 1.5;
  -webkit-font-smoothing: antialiased; /* WebKit-specific font smoothing */
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6,
button,
cite {
  overflow-wrap: break-word;
  font-family: medieval;
}

#root,
#__next {
  isolation: isolate;
}

html {
  font-size: calc(16px + 0.1vw);
}

body {
  background: #aa0000;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___}),
    url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-repeat: repeat-x, repeat;
}

header {
  /* background-color: #0ec035; */
  position: relative;
  width: 40rem;
  gap: 1rem;
  top: 1rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

#title-div {
  /* background-color: aquamarine; */
  display: flex;
  justify-content: center;
  /* justify-content: space-between; */
  gap: 4rem;
  width: 30rem;
}

.pizzeros-logo {
  position: relative;
  top: 0.5rem;
  width: 3rem;
  height: 100%;
}

#motto-container {
  display: flex;
  justify-content: center;
}

#motto {
  margin-top: 0.5rem;
}

h1 {
  display: flex;
  font-size: 3rem;
  justify-content: center;
  color: rgb(255, 3, 3);
}

#tab-form {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}

label {
  position: relative;
  bottom: 0.4rem;
  font-size: 1.2rem;
  font-family: medieval;
}

#content {
  /* background-color: yellow; */
  overflow: auto;
  position: relative;
  top: 5rem;
  width: 60vw;
  margin: 0 auto; /* Center content without overflow */
  max-height: 90rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center top; /* Ensure background image is centered */
  -webkit-background-size: 100%;
  -webkit-background-position: center top; /* WebKit-specific background adjustments */
}

#inner-content {
  /* background-color: aqua; */
  position: relative;
  top: 10vw;
  width: 35%;
  height: 115rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

#pizza-world-image {
  position: relative;
  top: 2vw;
  left: 50%; /* Center image horizontally */
  transform: translateX(-50%); /* Ensure proper centering */
  width: 15rem;
}

figcaption {
  display: flex;
  justify-content: end;
  position: relative;
  top: 2rem;
}

#telephone-main {
  margin-bottom: 18rem;
  font-size: 2rem;
  color: #ff0303;
}

input[type="radio"] {
  -webkit-appearance: none; /* Removes default radio button styles on WebKit */
  appearance: none;
  background-color: #babec7;
  margin: 0;
  font: inherit;
  width: 1.15em;
  height: 1.15em;
  border: 0.15em solid black;
  border-radius: 50%;
}

input[type="radio"]:hover {
  background-color: #49536d;
}

input[type="radio"]:checked {
  border: none;
  background: none;
  content: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
  position: relative;
  bottom: 1rem;
  width: 3rem;
  height: 100%;
}

/* MENU======================================================================= */
#choice-title-container {
  display: flex;
  justify-content: center;
}

#wheel-figure {
  position: relative;
  top: 2rem;
  display: flex;
  justify-content: center;
}

#div-button-container {
  position: relative;
  top: 2rem;
  display: flex;
  justify-content: center;
}

#spin-button {
  padding: 0.2rem 2rem;
  border-color: #ff0303;
  border-radius: 0.5rem;
}

#result-container {
  position: relative;
  top: 2rem;
  width: 100%;
  display: flex;
  justify-content: center;
}

#arrow-container {
  display: flex;
  justify-content: center;
}

#arrow-image {
  z-index: 1;
  width: 1.6rem;
  position: absolute;
  top: 7.5rem;
}

/* About ================================= */
#contact-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  gap: 1.5rem;
}

.icon {
  width: 1.5rem;
}

.contact-li {
  display: flex;
  justify-content: start;
  flex-direction: row;
  gap: 1rem;
}

/* Responsive Design */
@media screen and (min-width: 1060px) {
  #content {
    width: 100%;
  }
}

@media screen and (min-width: 1900px) {
  #content {
    width: 60%;
  }
}

@media screen and (max-width: 1059px) {
  html {
    font-size: 10px;
  }

  #content {
    overflow: scroll;
    max-height: 60rem;
  }

  #inner-content {
    top: 1rem;
    overflow: scroll;
    height: 20vh;
  }

  * {
    gap: 1rem;
  }
}
